import React, { Component } from 'react'
import debounce from 'lodash.debounce'

import Chooser from './Chooser'
import Interactive from './Interactive'
import Simple from './Simple'

export default class Page extends Component {

    state = {
        chosen: false,
        simple: true,
        mobile: false,
    }

    componentWillMount(){
        window.addEventListener('resize',this.checkMobile)
    }

    checkMobile = debounce(() => {
        if(window.innerWidth > 768){
            if(!this.state.mobile) this.setState({mobile: true})
        }
        else{
            if(this.state.mobile) this.setState({mobile: false})
        }
    },100)

    render(){
        return <div id="page-wrap">
            {this.state.chosen?
                this.state.simple?
                <Simple mobile={this.state.mobile}/>:<Interactive mobile={this.state.mobile}/> 
            :<Chooser mobile={this.state.mobile}/>}
        </div>
    }
}